var navMas = $('.services-nav__btn'),
    infoMas = $('.desc-item'),
    idNavAct, 
    idNavClick, 
    navClick;

function servisecAnima(){

    navMas.on('click', function(){
 
        navClick = $(this)[0];
        

        navMas.each(function(i, elem){
            
            if($(elem).hasClass('is-active')){
                idNavAct = i;                
            } 
            if($(elem)[0] == navClick){
                idNavClick = i;
            }           
        });

        if(idNavAct == idNavClick){
            return;
        }   

        navMas.each(function(i, elem){

            if(i == idNavAct){
                $(elem).removeClass('is-active');
            }
            else if(i == idNavClick){
                $(elem).addClass('is-active');
            }
        });

        anima(); 
        
    });
}

function anima() {
    var windowWidth = document.documentElement.clientWidth;

    if(windowWidth <= 768){

        infoMas.each(function(i, elem){
                
            if(i == idNavAct){
                $(elem).addClass("animated zoomOut");
                $(elem).removeClass('zoomIn is-active');
            }
            else if(i == idNavClick){
                $(elem).addClass("animated zoomIn is-active");
                $(elem).removeClass('zoomOut');
            }
        }); 
    }
    else{

        infoMas.each(function(i, elem){
                
            if(i == idNavAct){
                $(elem).addClass("animated fadeOutRightBig");
                $(elem).removeClass('fadeInLeftBig is-active');
            }
            else if(i == idNavClick){
                $(elem).addClass("animated fadeInLeftBig is-active");
                $(elem).removeClass('fadeOutRightBig');
            }
        });  
    }
}