var btn = $('.header-body__burger'),
    menu = $('.header-body'),
    mask = $('.mask'),
    form = $('.intro-form'),
    item = $('.header-nav__item').not('.item-not-animate'),
    contact = $('.item-contact'),
    home = $('.item-home'),
    body = $('body');

function menuShow(){
    btn.on('click', function(){
        menu.toggleClass('is-active');
        
        var windowWidth = document.documentElement.clientWidth;

        if(windowWidth <= 768){
            mask.toggleClass('is-active');
            body.toggleClass('overHidden');
        }
    });
    contact.on('click', function(){ 
        item.removeClass('is-active');
        home.removeClass('is-active');
        $(this).toggleClass('is-active');       
        form.toggleClass('is-active');
        body.toggleClass('overHidden');
        btn.click();
    });
    item.on('click',"a", function(event){

        event.preventDefault();
        var idAnchor = $(this).attr('href');  
        smoothTrans(idAnchor);

        if(contact.hasClass('is-active')){            
            item.removeClass('is-active');
            contact.toggleClass('is-active');       
            form.toggleClass('is-active');
            body.toggleClass('overHidden');
            console.log('item');
        }
        
        item.removeClass('is-active');
        home.removeClass('is-active');
        $(this).parent('.header-nav__item').toggleClass('is-active'); 
        btn.click();
    });
    home.on('click', function(){

        if(contact.hasClass('is-active')){            
            item.removeClass('is-active');
            contact.toggleClass('is-active');       
            form.toggleClass('is-active');
            body.toggleClass('overHidden');
        }
        console.log('home');
        
        item.removeClass('is-active');
        $(this).toggleClass('is-active'); 
        btn.click();
    });
    function smoothTrans(idAnchor) { 
        var top = $(idAnchor).offset().top;
        $('body,html').animate({scrollTop: top}, 600);
    }
}